                            v-model="select2"
                    >
                        <vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item,index) in options2" />
                    </vs-select>



                </div>
            </div>

            <div class="mb-4">
                <label class="block text-black text-sm font-bold mb-2">
