v-model.number=\"sms_quantity\" class=\"shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline\" type=\"number\">\n            </div>\n            <div class=\"mb-4 text-center\">\n\n                {{ t('price') }}: <span class=\"text-ice-blue font-bold\" v-model.number=\"final_price\">{{formatPrice((sms_quantity > 4999 && sms_quantity < 99999 ? 0.23 : (sms_quantity > 99999 ? 0.20 : 0.25)) * sms_quantity)}}</span>\n                <span class=\"text-sm text-gray-500\">{{ t('lei') }}</span>\n\n            </div>\n            <div class=\"mb-4\">\n                <label class=\"block text-black text-sm font-bold mb-2\">\n                    {{ t('business_describe') }}\n                </label>\n            <textarea class=\"w-full shadow-inner bg-gray-100 p-4 border-1\" :placeholder=\"t('business_describe_example')\" rows=\"1\"></textarea>\n            </div>\n            <div class=\"mb-4\">\n                <label class=\"block text-black text-sm font-bold mb-2\">\n                    {{ t('target_audience_describe') }}\n                </label>\n                <textarea class=\"w-full shadow-inner bg-gray-100 p-4 border-1\" :placeholder=\"t('target_audience_describe_example')\" rows=\"3\"></textarea>\n            </div>\n            <div class=\"flex justify-end pt-2\">\n                <a href=\"#\" class=\"modal-close px-4 p-3 text-ice-blue hover:text-grey-700\">{{ t('close') }}</a>\n                <button class=\"px-4 bg-ice-blue p-3 rounded-full text-white hover:bg-gray-100 hover:text-ice-blue hover:border-ice-blue border-t border-b border-r border-l mr-2\">{{ t('send') }}</button>\n            </div>\n            <link ref=\"stylesheet\" href=\"./vue-bootstrap-selectpicker.min.css\">\n        </form>\n\n    </div>\n</template>\n\n<script>\n    import i18n_mixin from \"./mixins/i18n_mixin\";\n\n    export default {\n        props: ['sms_quantity'],\n        mixins: [i18n_mixin],\n        data () {\n            return {\n                select2:[],\n                options2:[\n                    {text: 'Square', value: 1},\n                    {text: 'Rectangle', value: 2},\n                    {text: 'Rombo', value: 3},\n                    {text: 'Romboid', value: 4},\n                    {text: 'Trapeze', value: 5},\n                    {text: 'Triangle', value: 6},\n                    {text: 'Polygon', value: 7},\n                    {text: 'Regular polygon', value: 8},\n                    {text: 'Circumference', value: 9},\n                    {text: 'Circle', value: 10},\n                    {text: 'Circular sector', value: 11},\n                    {text: 'Circular trapeze', value: 12},\n                ],\n                name: null,\n                final_price: null,\n            }\n        },\n        methods: {\n            formatPrice(value) {\n                let val = (value/1).toFixed(2).replace('.', ',')\n                return val.toString().replace(/\\B(?=(\\d{3})+(?!\\d))/g, \".\")\n            }\n        }\n    }\n</script>\n\n<style scoped>\n    .modal {\n        transition: opacity 0.25s ease;\n    }\n</style>"]}]);

